@media (max-width: 575px) {
  .slider-height-padding {
    padding-top: 240px;
  }
  ul li {
    height: 200px;
  }
  .highlights {
    font-size: 22px;
  }
}

.location .info-box {
  color: #444444;
  text-align: center;
  box-shadow: 0 0 10px #5e887c;
  padding: 20px 0 30px 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.circulerIcon {
  font-size: 32px;
  color: #e6ece2;
  border-radius: 50%;
  padding: 8px;
  border: 2px dotted #e6ece2;
}

.location .info-box h3 {
  font-size: 20px;
  color: #1d4e55;
  font-weight: 700;
  margin: 10px 0;
}

.location .info-box p {
  padding: 0;
  color: #000;
  line-height: 24px;
  font-size: 14px;
  margin-bottom: 0;
}

.videoDiv {
  position: relative;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: #fff;
  background-color: #000000;
  text-decoration: none;
  max-height: 400px;
  overflow: hidden;
  z-index: 1;
}

#myVideo {
  width: 100%;
}

#videoMessage {
  position: absolute;
  top: 0;
  left: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 100%;
}

#videoMessage {
  color: #ffffff;
  z-index: 99;
}

#videoMessage h1 {
  position: absolute;
  font-family: -apple-system, BlinkMacSystemFont, segoe ui, roboto, oxygen,
    cantarell, helvetica neue, ubuntu, sans-serif;
  font-size: 5vw;
  color: #ffffff;
  text-align: center;
  text-shadow: 1px 1px 1px #919191, 1px 1px 2px #919191, 1px 1px 3px #919191,
    1px 1px 4px #919191, 1px 1px 5px #919191, 1px 1px 6px #919191,
    1px 1px 7px #919191, 1px 1px 8px #919191, 1px 1px 9px #919191,
    1px 18px 6px rgba(16, 16, 16, 0.4), 1px 22px 10px rgba(16, 16, 16, 0.2),
    1px 25px 35px rgba(16, 16, 16, 0.2), 1px 30px 60px rgba(16, 16, 16, 0.4);
}

.highlight-section {
  background-color: #a0b3a8d5;
}
.video-responsive {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  height: 0;
}

.video-responsive iframe {
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}

