html {
  position: relative;
  min-height: 100vh;
}

body {
  font-family: "Tinos", serif;
  font-size: 100%;
  background-color: #99aba1d5;
  margin-bottom: 120px;
}

/* SCROLL BAR*/
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #0e3742b4;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #0e3742;
}
